import React from "react";

import LightFAQItem from "components/LightFAQItem";
import { Main } from "components/solution/elements";

const faqs = [
  {
    question: "What does no interest credit card mean?",
    answer: (
      <span>
        A no interest credit card is a credit card that charges you 0% APR on
        your balance. This is commonly due to an introductory offer, meaning
        interest will only be applied on balances after a set period. Corporate
        credit cards and business charge cards also charge no interest, as the
        issuer requires the account holder to pay any debts in full by the end
        of the billing cycle.
      </span>
    ),
  },
  {
    question: "How do Zero interest credit cards work?",
    answer: (
      <span>
        Credit cards with a zero-interest introductory offer will not charge any
        interest on purchases for a set period. Typically, these introductory
        offers last between 12 to 22 months, making them ideal for balance
        transfers. Once the zero-interest period comes to an end, the issuers
        regular interest rate will apply.
        <br />
        <br />
        Business charge cards or corporate credit cards that have zero interest
        do not allow the user to carry a balance. Full payment must be made each
        billing cycle to avoid incurring late fees and account restrictions.
      </span>
    ),
  },
  {
    question: "Does having a zero balance affect your credit score?",
    answer: (
      <span>
        Having large balances on your credit card can harm your credit score.
        This is because increasing your balance will also increase your credit
        utilization ratio, which is a significant factor in determining your
        creditworthiness. Your credit utilization ratio is the percentage of
        your total credit limit that has been utilized, and it is a good idea to
        keep this below 30%.
        <br />
        <br />
        In general, having a zero balance should not affect your credit score.
        However, if this is due to inactivity on your account, then it may have
        a negative impact on your credit score over time.
      </span>
    ),
  },
];

const FAQ = () => (
  <Main>
    {faqs.map((faq) => (
      <LightFAQItem {...faq} key={faq.question} />
    ))}
  </Main>
);

export default FAQ;
